import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';

import Api from '~/api';
import router from '~/router';

@Component({})
export default class LoginComp extends Vue {

    // tel = '13925210768';
    // tel = '18312288722';
    tel = '13925210035';
    // tel = '13510800101';
    // tel = '17326000571';

    version = '0.1';

    @Action('initUser')
    initUser;

    async login() {
        const timestamp = new Date().getTime();
        const ver = '';
        const res = await Api.login(timestamp, this.tel, this.version, null);

        // 初始化 个人各种信息
        await this.initUser();
        // 跳转到tab.ai页面
        router.push({
            name: 'ai'
        });
    }   
}



